import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Contact } from '../_models/contact';
import { User } from '../_models/user';
import { AlertifyService } from '../_services/Alertify.service';
import { AuthService } from '../_services/Auth.service';

@Component({
  selector: 'app-contact-us',
  templateUrl: './contact-us.component.html',
  styleUrls: ['./contact-us.component.css']
})
export class ContactUsComponent implements OnInit {
  contact: Contact;
  currentUser: User;
  registerForm: FormGroup;
  colorTheme = 'theme-red';

  constructor(private router: Router, private authService: AuthService, private alertify: AlertifyService, private fb: FormBuilder) { }

  ngOnInit() {
    this.currentUser = JSON.parse(localStorage.getItem("currentUser"));

    this.registerForm = this.fb.group({
      name: [this.currentUser.knownAs, Validators.required],
      email: [this.currentUser.email, Validators.email],
      message: ['', Validators.required]
    });
  }

  register() {
    this.contact = Object.assign({}, this.registerForm.value);
    this.contact.memberId = this.currentUser.memberId;
    this.authService.contactUs(this.contact).subscribe(next => {
      this.alertify.success('Contato enviado com Sucesso!');
      this.registerForm.reset()
    }, error => {
      console.log(error);
      this.alertify.error('Erro ao enviar contato! Envie pelo Insta @VaraoVaroa');
    });
  }
}