import { Component, OnInit } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { CREATE_FORM, environment } from 'src/environments/environment';
import { AuthService } from '../_services/Auth.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  showHideRegister: boolean;
  createUrl: SafeResourceUrl;
  loading: boolean;
  version: string = environment.appVersion;
  year: number = new Date().getFullYear();
  
  constructor(public authService: AuthService, private router: Router, private spinner: NgxSpinnerService, private sanitizer: DomSanitizer) { }

  ngOnInit() {
    //TODO Não esta acionando o Spinner
    this.createUrl = this.sanitizer.bypassSecurityTrustResourceUrl(`https://airtable.com/embed/${CREATE_FORM}`);
  }

  hideSpinner() {
    // this.spinner.hide();
    this.loading = false;
  }

  loggedIn() {
    var isLogged = this.authService.loggedIn();
    return isLogged;
  }

  toggleRegister() {
    this.showHideRegister = !this.showHideRegister;
    // this.spinner.show();
    if (this.showHideRegister) this.loading = true;
  }

  toggleLists() {
    this.router.navigate(['/members']);
  }
}
