  saveLike(recipientMemberId: string, isLiked: boolean) {
    const httpHeaders = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'Bearer ' + API_KEY });

    const memberId4 = recipientMemberId.substring(recipientMemberId.length - 4);
    const memberId = this.authService.getCurrentUser().memberId;

    let favorites = localStorage.getItem("favorites");

    if (isLiked) {
      favorites += `${memberId4},`;
    }
    else {
      favorites = favorites.replace(`${memberId4},`, "");
    }
    localStorage.setItem("favorites", favorites);

    return this.http.patch<any>(API_PATH + 'Members/' + memberId,
      JSON.stringify({ "fields": { "Favorites": favorites }, "typecast": true }),
      { headers: httpHeaders });
  }

  deleteUser(memberId: string) {
    const headers = new HttpHeaders().set('Authorization', 'Bearer ' + API_KEY);

    return this.http.delete(API_PATH + 'Members/' + memberId, { headers: headers })
      .pipe(map((response: any) => {
        if (response) {
          const id = response.id;
          return true;
        }
        return false;
      }));
  }

  updateUser(id: number, user: User) {
    return this.http.put<User>(API_PATH + 'users/' + id, user);
  }

  setMainPhoto(userId: number, id: number) {
    return this.http.post(API_PATH + 'users/' + userId + '/photos/' + id + '/setMain', {});
  }

  deletePhoto(userId: number, id: number) {
    return this.http.delete(API_PATH + 'users/' + userId + '/photos/' + id);
  }



  markAsRead(userId: number, messageId: number) {
    this.http.post(API_PATH + 'users/' + userId + '/message/' + messageId + '/read', {})
      .subscribe();
  }
