import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { Message } from '../_models/message';
import { Pagination, PaginatedResult } from '../_models/pagination';
import { AuthService } from '../_services/Auth.service';
import { UserService } from '../_services/user.service';
import { AlertifyService } from '../_services/Alertify.service';

@Component({
  selector: "app-messages",
  templateUrl: "./messages.component.html",
  styleUrls: ["./messages.component.css"],
})
export class MessagesComponent implements OnInit {
  messages: Message[];
  pagination: Pagination;
  messageContainer = 'Unread';

  constructor(private userService: UserService, private alertifyService: AlertifyService,
    private route: ActivatedRoute, private authService: AuthService) { }

  ngOnInit(): void {
    this.route.data.subscribe(data => {
      this.messages = data['messages'].result;
      this.pagination = data['messages'].pagination;
    });
  }

  loadMessages() {
    this.userService.getLastChats(this.authService.getCurrentUser().memberId, this.pagination.currentPage,
      this.pagination.itemsPerPage, this.messageContainer)
      .subscribe((data: PaginatedResult<Message[]>) => {
        this.messages = data.result;
        this.pagination = data.pagination;
      }, error => {
        this.alertifyService.error(error);
      });
  }

  pageChanged(event: any): void {
    this.pagination.currentPage = event.page;
    this.loadMessages();
  }

  deleteMessage(message: Message, event) {
    event.stopPropagation();

    this.alertifyService.confirm('Deletar Conversa?', 'Tem certeza que deseja deletar toda esta conversa?', () => {
      this.userService.deleteLastChat(message)
        .subscribe(result1 => {
          
          let result = this.userService.deleteChats(message);
          let test = result;
            
        }, error => {
          this.alertifyService.error('Falha ao deletar conversa');
        });
    });
  }
}
