import { MemberReloadComponent } from './member-reload/member-reload.component';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { HomeComponent } from './home/home.component';
import { MemberListComponent } from './members/member-list/member-list.component';
import { MessagesComponent } from './messages/messages.component';
import { ListsComponent } from './lists/lists.component';
import { AuthGuard } from './_guards/auth.guard';
import { Routes } from '@angular/router';
import { MemberDetailComponent } from './members/member-detail/member-detail.component';
import { MemberListResolver } from './_resolvers/member-list.resolver';
import { MemberDetailResolver } from './_resolvers/member-detail.resolver';
import { MemberEditComponent } from './members/member-edit/member-edit.component';
import { MemberEditResolver } from './_resolvers/member-edit.resolver';
// import { PreventUnsavedChanges } from './_guards/prevent-unsaved-changes.guard';
import { ListsResolver } from './_resolvers/lists.resolver';
import { MessageListResolver } from './_resolvers/message-list.resolver';

export const appRoutes: Routes = [
    {path: '', component: HomeComponent},
    {path: 'contactus', component: ContactUsComponent},
    {path: 'reload', component: MemberReloadComponent},
    {
        path: '',
        runGuardsAndResolvers: 'always',
        canActivate: [AuthGuard],
        children: [
            { path: 'members', component: MemberListComponent, resolve: { users: MemberListResolver} },
            { path: 'members/:id', component: MemberDetailComponent, resolve: { user: MemberDetailResolver} },
            { path: 'members/:user', component: MemberDetailComponent, resolve: { user: MemberDetailResolver} },
            // { path: 'member/edit', component: MemberEditComponent, canDeactivate: [PreventUnsavedChanges], resolve: { user: MemberEditResolver}},
            { path: 'member/edit', component: MemberEditComponent, resolve: { user: MemberEditResolver}},
            { path: 'messages', component: MessagesComponent, resolve: { messages: MessageListResolver} },
            { path: 'lists', component: ListsComponent, resolve: { users: ListsResolver}}
        ]
    },
    {path: '**', redirectTo: '', pathMatch: 'full'}
];
